:root {
  --clr-almost-white: #f5f5f5;
  --clr-almost-black: #333;
  --clr-gray: #6c757d;
  --clr-light-gray: lightgray;
  --clr-enter: #007bff;
  --clr-success: #28a745;
  --clr-alert: #dc3545;
}

body {
  color: var(--clr-almost-black);
  background-color: var(--clr-almost-black);
  font-family: Inter, sans-serif;
  font-size: 1em;
  line-height: 1em;
}

h1 {
  margin-bottom: 15px;
  font-size: 1.5em;
  font-weight: 700;
  line-height: 1.5em;
}

.container {
  color: var(--clr-back);
  background-color: var(--clr-almost-white);
  border: 1px solid var(--clr-almost-white);
  border-radius: 10px;
  max-width: 600px;
  margin: 0 auto;
  padding: 30px 15px;
}

.new-item {
  justify-content: space-between;
  gap: 10px;
  margin-bottom: 15px;
  display: flex;
}

.new-item-input {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  background-color: inherit;
  border: none;
  border-bottom: 1px solid #000;
  width: 100%;
  padding: 5px;
}

.new-item-btn {
  border: 1px solid var(--clr-almost-black);
  cursor: pointer;
  background-color: #0000;
  background-image: url("img_add_task.d5061ced.png");
  background-size: contain;
  border-radius: 5px;
  width: 2em;
  height: 2em;
}

.new-item-btn:hover {
  opacity: .5;
}

.display-item-wrapper {
  border: 1px solid var(--clr-almost-black);
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  height: 2.5em;
  margin-bottom: 3px;
  display: flex;
}

.item-checkbox {
  width: 2em;
  height: 2em;
}

.display-item {
  width: 100%;
  padding: 0 10px;
  display: block;
}

.item-delete-btn, .item-hide-btn {
  cursor: pointer;
  background-color: #0000;
  background-image: url("img_close.05edba8a.png");
  background-size: contain;
  border: none;
  border-radius: 5px;
  width: 2em;
  height: 2em;
}

.completed {
  background-color: var(--clr-light-gray);
  opacity: .5;
  text-decoration: line-through;
}

.trash-switch {
  border: 1px solid var(--clr-almost-black);
  cursor: pointer;
  background-color: #0000;
  background-image: url("img_delete.c9054875.png");
  background-size: contain;
  border-radius: 5px;
  width: 2em;
  height: 2em;
  margin-top: 15px;
  margin-bottom: 15px;
}

.restore-btn {
  cursor: pointer;
  background-color: #0000;
  background-image: url("img_restore_from_trash.a31c8fa1.png");
  background-size: contain;
  border: none;
  border-radius: 5px;
  width: 2em;
  height: 2em;
}

